
<template>
  <div class="Movie">
    <!-- 头部 -->
    <Movie-header :title="'猫眼电影'" :type="'movie'"></Movie-header>
    <!-- 电影 - 主体 -->
    <main class="main-index">
      <!-- 电影 -->
      <div class="Movie-main">
        <!-- 电影 - 块 -->
        <section class="Movie-row">
          <ul class="Movie-row-ul">
            <!-- row -->
            <li class="Movie-row-img" v-for="i in msg.movieList" :key="i.id">
              <!-- 电影图片 -->
              <div class="Movie-row-img">
                <img :src="i.img" alt height="100" />
              </div>
              <!-- 电影内容 -->
              <div class="Movie-row-content">
                <h3>{{i.nm}}</h3>
                <p>
                  观众点评
                  <span class="review">{{i.sc}}</span>
                  <br />
                  <span class="starring">主演 {{i.star}}</span>
                  <!-- 播放 -->
                  {{i.showInfo}}
                </p>
              </div>
              <!-- 交互按钮 -->
              <div class="Movie-row-interactive">
                <button>购票</button>
              </div>
            </li>
            <!-- row - end -->
          </ul>
        </section>
      </div>
    </main>
    <!-- 脚注 - 导航 -->
    <Movie-footer></Movie-footer>
  </div>
</template>


<script>
import header from "@/components/Movie-header";
import footer from "@/components/Movie-footer";
import axios from "axios";
import qs from "qs";

export default {
  name: "Movie",
  data() {
    return {
      msg: []
    };
  },
  methods: {
    //  获取JSON文件接口
    OPEN_MovieApi() {
      axios
        .get(`/server/movie/movieOnInfoList.json`)
        .then(res => {
          // 存入
          this.msg = res.data;
          //   console.log(this.msg);
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  mounted() {
    //   获取电影数据
    this.OPEN_MovieApi();
  },
  components: {
    "Movie-header": header,
    "Movie-footer": footer
  }
};
</script>



<style scoped>
/* 电影 - 块 */
.Movie-row {
}
.Movie-row-ul {
}
.Movie-row-ul > li {
  display: flex;
  padding: 20px 10px;
  width: 100%;
  box-sizing: border-box;
}

@import '../../../public/css/movie.css';
</style>

